@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
    -webkit-filter: grayscale(100%) blur(2px);
            filter: grayscale(100%) blur(2px);

}

.home-container{
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    object-fit: contain;
}

.home-container > h1{
    color: #3699cf;
    font-size: 100px;
    margin-left: 400px;
    margin-top: 400px;
}
.Navlink{
    font-size: 40px;
    margin-left: 600px;
    background:#3699cf 90%;
    color: #ffffff;
}
.app {
    text-align: center;
    background-color: #282c34;
    min-height: 40vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .time {
    font-size: 3rem;
    padding: 2rem;
  }
  
  .button {
    padding: .6rem 1.5rem;
    margin: .4rem;
    border-radius: 3px;
    text-transform: uppercase;
    font-weight: 600;
    font-size: .8rem;
    border-style: groove;
  }
  
  .button:focus {
    outline-width: 0;
  }
  
  .button-primary:hover {
    background-color: #2641d4;
    border: 1px solid #1b1f2b;
  }
  
  .button-primary-active {
    background-color: #3151ff;
    border: 1px solid #152684;
    color: white;
  }
  
  .button-primary-inactive {
    background-color: #3151ff;
    border: 1px solid #152684;
    color: white;
  }
* {
  box-sizing: border-box;
}

body {
  background-image: url("https://images.unsplash.com/random");
  background-size: cover;
  background-position: center center;
  display: flex;
  font-family: "Lato", sans-serif;
  font-size: 140%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
  min-height: 100vh;
}

h4 {
  margin: 0 0 5px;
}

progress {
  width: 100%;
}

p {
  line-height: 1.5;
}

.container {
  box-shadow: 0 4px 15px -5px rgba(0, 0, 0, 0.7);
  background-color: #fff;
  border: 2px solid #000;
  padding: 30px;
  width: 650px;
  max-width: 100%;
  text-align: center;
}

.container .text {
  display: flex;
  flex-wrap: wrap;
}

.container input {
  padding: 12px 15px;
  font-size: 20px;
  margin: 10px 0;
  width: 100%;
}

.start-btn {
  border: 0;
  background-color: #000;
  color: #fff;
  font-size: 16px;
  padding: 12px 15px;
}

.wpm {
  position: fixed;
  top: 20px;
  right: 20px;
  text-align: right;
}

.green {
  color: green;
}

.red {
  background-color: rgba(255, 0, 0, 0.5);
}

.underline {
  border-bottom: 1px solid #000;
}

.word {
  font-size: 20px;
  margin: 2px;
}

.share {
  color: #38a1f3;
}

@media screen and (max-width: 650px) {
  p {
    line-height: 1.3;
  }

  .container {
    margin-bottom: 50px;
    padding: 10px;
    width: 100%;
  }

  .wpm {
    display: none;
  }
}

.LangButton{
    height:100px;
    width:200px;
    margin: 20px;
    background-color: #ffffff;
    border: 0.08em solid;
    color: while;
    padding: 10px;
    text-align: center;
    display: grid;
    font-size: 16px;
}
